import { newSpecPage } from '@stencil/core/testing';
import { h } from '@stencil/core';
import { PrestoSearchBar } from '../presto-search-bar';

describe('presto-search-bar', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoSearchBar],
      template: () => <presto-search-bar></presto-search-bar>,
    });
    expect(page.root).toEqualHtml(`
    <presto-search-bar></presto-search-bar>
    `);
  });
});
